body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.btn{
  border: none;
  display: inline-block;
  width: 55px;
  margin: 10px auto;
  padding: 10px 0px;
  background: #3a9ab3;
  border: 1px solid #0b1f24;
  font-weight: 600
  }
  
  .btn:hover{
    background: black;
    color: white;
  }
  
  .counter{
    color: #eceff5;
    border: 1px solid;
    width: 55px;
    margin: 10px auto;
    padding: 10px 20px;
    background: rgb(8, 34, 49)
  }
